.invoice {
  margin: 20px 20px;
  padding: 25px 0px;
  border-radius: 20px;
  margin-bottom: 100px;
  width: 100%;
  max-width: 100%;
  margin-top: 0;
  padding-top: 0;
}

.header-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  /* margin-bottom: 20px; */
}
.info-header {
  width: 100%;
  text-align: start;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
}
.header-title i {
  cursor: pointer;
  transition: 0.7s;
  margin-right: 20px;
  padding: 10px;
}

.header-title i:hover {
  color: crimson;
}

.other-amounts {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  margin: 10px 0;
  font-size: 13px;
}

.discount-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid rgb(211, 211, 211);
  border-radius: 10px;
  width: 100%;
  padding: 10px 25px;
}
.discount-wrapper input {
  width: 75px;
  padding: 5px;
  border: none;
  outline: none;
  border-bottom: 1px solid lightgray;
}
.discount-wrapper div {
  display: flex;
  align-items: center;
}
.discount-wrapper label {
  flex-grow: 1;
}

.payments {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  grid-gap: 20px;
  border: 1px solid lightgray;
  margin: 15px 0;
  align-items: center;
  font-size: 13px;
}

.payment-card {
  display: flex;
  justify-content: start;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
}
.payment-card label,
.discount-wrapper label {
  cursor: pointer;
}

.payment-card:hover {
  color: crimson;
}

.payment-card:hover input {
  background-color: crimson;
}

.payment-card label {
  padding: 0 10px;
}

.get-result {
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 25px;
  font-size: 11px;
  margin-bottom: 30px;
}
.get-result .result-card {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid crimson;
  border-radius: 10px;
  padding: 10px;
  transition: 0.5s;
  cursor: pointer;
}
.get-result .result-card i {
  font-size: 15px;
  margin-right: 5px;
}
.get-result .result-card:hover {
  color: #f5f5f5;
  background: crimson;
}
/*  responsive /////////////////////////////////////////////////////////////// */
@media (max-width: 1250px) {
  .other-amounts {
    grid-template-columns: repeat(4, 1fr);
  }
  .payments {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 1000px) {
  .other-amounts {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 780px) {
  .other-amounts {
    grid-template-columns: repeat(2, 1fr);
  }
  .payments {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 600px) {
  .other-amounts {
    grid-template-columns: repeat(1, 1fr);
  }
  .payments {
    grid-template-columns: repeat(2, 1fr);
  }
  .get-result {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 5px;
    margin-bottom: 10px;
    margin-top: -25px;
  }
}
