.info-table {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  /* padding: 20px;
  margin-left: 20px; */
  /* margin-right: 20px; */
}
.info-table .info-table-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 100%;
  width: 100%;
  padding: 5px;
  margin: 3px 6px;
  overflow-x: scroll;
}

.info-table .info-table-container .invoice-result {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  /* min-width: 300px; */
  border: 2px solid #333;
  border-radius: 15px;
  padding: 5px;
  padding-bottom: 3px;
  margin-top: 5px;
  font-size: 10px;
  font-weight: 700;
}
.info-table .info-table-container .invoice-result .result-line {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 3px;
  padding: 1px 0;
  border-bottom: 1px solid lightgray;
}
.result-line-title-last {
  display: flex;
  justify-content: flex-end;
}
.result-line-title-first {
  display: flex;
  justify-content: flex-start;
}
.result-line-title-second {
  display: flex;
  justify-content: center;
}
.info-table-container::-webkit-scrollbar {
  width: 20px; /* width of the entire scrollbar */
}

.info-table-container::-webkit-scrollbar-track {
  /* background: gray; */
  border-radius: 10px;
}

.info-table-container::-webkit-scrollbar-thumb {
  background-color: #e7e7e7;
  border-radius: 10px;
  border: 1px solid white;
}

.invoice-table {
  border: 2px solid #333;
  border-radius: 20px;
  /* padding: 2px; */
  /* margin: 0px; */
  /* background-color: #333333; */
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border-spacing: 0;
  font-weight: 700;
}

.result-divider {
  /* height: 3px; */
  border: 2px solid #333;
  /* background: #333; */
  padding: 0;
  margin: 2px -5px;
}
