.table-heading-parent {
  width: 100%;
  min-width: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 9px;
  background: #c4c4c4e5;
  color: #1e1e1e;
  /* border-radius: 10px; */
  flex-wrap: nowrap;
  /* margin-bottom: 2px; */
  height: 30px;
  /* border-top-right-radius: 10px;
  border-top-left-radius: 10px; */
  border-bottom: 2px solid #1e1e1e;
  border-top: 2px solid #1e1e1e;
}

.table-heading-parent .table-heading {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-left: 1px solid #ffffff36; */
  padding: 5px;
  text-align: center;
  font-weight: 600;
  /* flex-grow: 1; */
}
.table-heading-parent .table-heading:first-child {
  border-left: none;
  /* margin-left: 5px; */
}

.table-heading-parent .quantity,
.table-heading-parent .row {
  min-width: 20px;
  max-width: 20px;
}
.table-heading-parent .edit {
  min-width: 30px;
  max-width: 30px;
  margin: 2px;
}

.table-heading-parent .width,
.table-heading-parent .length {
  /* flex-grow: 1; */
  min-width: 65px;
  /* max-width: 65px; */
}

.table-heading-parent .area,
.table-heading-parent .comment {
  /* flex-grow: 5; */
  min-width: 60px;
  /* max-width: 60px; */
}

.table-heading-parent .manufacture,
.table-heading-parent .category,
.table-heading-parent .lightcontrol,
.table-heading-parent .fabric {
  min-width: 60px;
  /* max-width: 60px; */
}
.table-heading-parent .color,
.table-heading-parent .Manual,
.table-heading-parent .position,
.table-heading-parent .layout,
.table-heading-parent .unit-price,
.table-heading-parent .total-price {
  /* flex-grow: 2; */
  min-width: 40px;
  /* max-width: 40px; */
}
.table-heading-parent .cassette-type,
.table-heading-parent .cassette-color,
.table-heading-parent .placement {
  min-width: 55px;
  /* max-width: 55px; */
}
.table-heading-parent .Motorized {
  min-width: 75px;
  /* max-width: 75px; */
}

.accessories-table-data-parent {
  border-radius: 0;
  border-top: 2px solid #1e1e1e;
  /* margin-top: 10px; */
}

.table-header {
  display: flex;
  margin-left: 10px !important;
  margin-top: 3px;
  margin-bottom: 3px;
  font-size: 11px;
}
