.customer-information {
  display: flex;
  flex-direction: column;
  padding: 0 10px;
}

.customer-information .information {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: start;
  font-size: 11px;
  font-weight: 600;
}
.customer-information .information strong {
  font-weight: 800;
}
.customer-information .information .info-left,
.customer-information .information .info-right {
  display: flex;
  flex-direction: column;
}

.invoice-header-last {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 5px;
  font-size: 15px;
}

.invoice-header-last img {
  width: 120px;
  margin-bottom: 7px;
}
.invoice-header-last p {
  text-align: center;
  font-size: 10px;
}
