.table-data-parent {
  width: 100%;
  min-width: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  background: #f5f5f5;
  flex-wrap: nowrap;
  /* margin-bottom: 2px; */
  overflow: hidden;
  /* border-radius: 10px; */
  border-bottom: 1px solid rgb(24, 24, 24);
}

.table-data-parent:nth-child(even) {
  background-color: #d6d6d6;
}

.table-data-parent:last-child {
  border-bottom: none;
  /* border-bottom-right-radius: 15px;
  border-bottom-left-radius: 15px; */
  /* padding-bottom: 3px; */
}

.table-data-parent .table-data {
  width: 100%;
  height: 100%;
  /* min-height: 43px; */
  min-height: 30px;
  border-left: 1px solid #1e1e1e23;
  padding: 1px 5px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table-data-parent .edit {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* margin: 2px; */
}

.edit-icon {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  /* color: crimson; */
  pointer-events: none;
  transition: 0.5s;
  /* margin: 5px 0; */
}

.edit-icon:hover {
  transform: scale(1.3);
}

.table-data-parent .table-data:first-child {
  border-left: none;
  /* margin-left: 5px; */
}

.table-data-parent .quantity,
.table-data-parent .row {
  min-width: 20px;
  max-width: 20px;
}
.table-data-parent .edit {
  min-width: 30px;
  max-width: 30px;
  margin: 2px;
  cursor: pointer;
}
.table-data-parent .width,
.table-data-parent .length {
  /* flex-grow: 1; */
  min-width: 65px;
  /* max-width: 65px; */
}

.table-data-parent .area,
.table-data-parent .comment {
  /* flex-grow: 5; */
  min-width: 60px;
  /* max-width: 60px; */
}
.table-data-parent .manufacture,
.table-data-parent .category,
.table-data-parent .lightcontrol,
.table-data-parent .fabric {
  min-width: 60px;
  /* max-width: 60px; */
}
.table-data-parent .color,
.table-data-parent .Manual,
.table-data-parent .position,
.table-data-parent .layout,
.table-data-parent .unit-price,
.table-data-parent .total-price {
  /* flex-grow: 2; */
  min-width: 40px;
  /* max-width: 40px; */
}
.table-data-parent .cassette-type,
.table-data-parent .cassette-color,
.table-data-parent .Motorized,
.table-data-parent .placement {
  min-width: 55px;
  /* max-width: 55px; */
}
.table-data-parent .Motorized {
  min-width: 75px;
  /* max-width: 75px; */
}

.accessories-table {
  border: none !important;
}
