.get-accessories-form {
  width: 100%;
  display: grid;
  grid-gap: 15px;
  grid-template-columns: repeat(3, 1fr);
}

.form-button {
  grid-column: 1 / -1;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  padding: 0;
  border: none;
  height: 40px;
}

.get-accessories-form .form-button button {
  border: none;
  color: #f5f5f5;
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s;
  border-radius: 10px;
  text-transform: uppercase;
  font-family: Poppins;
}

.get-accessories-form button:hover {
  opacity: 60%;
}
.get-accessories-form .button-reset {
  background-color: crimson;
}

.get-accessories-form .button-submit {
  background-color: green;
}

.get-accessories-form > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-accessories-form > * :focus {
  border-color: rgb(220, 20, 60, 0.4);
}
.get-accessories-form .form-size > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}
.get-accessories-form .form-size > *:focus {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-accessories-form .dropdown-parent > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-accessories-form .dropdown-parent > * :focus {
  border-color: rgb(220, 20, 60, 0.4);
}
